.header {
  background-color: black;
  height: 70px;
  position: sticky;
  display: flex;
  justify-content: space-between;
}

img {
  margin: 6px;
}

.profile {
  background-color: rgb(89, 89, 204);
  color: aliceblue;
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 4px;
  border-radius: 2px;
  text-decoration: none;
}

.profileIcon {
  width: 1.5em !important;
  height: 2em !important;
}
